import React from 'react'

function Button({ btnClick, iconClick, btnText, iconTxt }) {
    return (
        <div className="checkAvailabilityButton">
            <div className="backBtn" style={iconTxt ? { display: 'flex' } : { display: 'none' }}>
                <i className={`bi ${iconTxt}`} onClick={() => iconClick()}></i>
            </div>

            <div className='button' onClick={() => btnClick()}>{btnText}</div>
        </div>
    )
}

export default Button