import React, { useState } from 'react'
import { nextToPage3, prevToPage1 } from '../Redux/Slices/StatusSlice'
import { useDispatch } from "react-redux"
import Button from '../components/Button/Button'
import { useRef } from 'react'


function Display2() {
    const dispatch = useDispatch()

    const imagesArr = ['https://bityl.co/GVhp', 'https://bityl.co/GYRh', 'https://bityl.co/GYS0']

    //STATES
    const [room, setRoom] = useState(1)
    const [data, setData] = useState(true)
    ////////////////////////////////

    //REF
    const img1 = useRef()
    const idRef = useRef(0)

    //EVENTS
    const addRoom = () => { setRoom((prev) => prev + 1) }

    const subtractRoom = () => {
        if (room === 1) return
        setRoom((prev) => prev - 1)
    }

    //ROUTE EVENTS
    const checkAndProceedToNextPage = () => {
        dispatch(nextToPage3())
    }

    const takeMeBack = () => {
        dispatch(prevToPage1())
    }

    //IMAGE EVENTS
    const NextImage = () => {
        img1.current.style.opacity = '0.1'
        img1.current.style.transition = 'opacity 0.2s ease'

        setTimeout(() => {
            idRef.current = idRef.current < imagesArr.length - 1 ? idRef.current + 1 : imagesArr.length - 1
            img1.current.src = imagesArr[idRef.current]
            img1.current.style.opacity = '1'
            img1.current.style.transition = 'opacity 0.2s ease'
        }, 250);
    }

    const PrevImage = () => {
        img1.current.style.opacity = '0.1'
        img1.current.style.transition = 'opacity 0.2s ease'

        setTimeout(() => {
            idRef.current = idRef.current > 0 ? idRef.current - 1 : 0
            img1.current.src = imagesArr[idRef.current]
            img1.current.style.opacity = '1'
            img1.current.style.transition = 'opacity 0.2s ease'
        }, 250);
    }


    return (
        <>
            <div className="body">
                {
                    data ?
                        <>
                            <div style={{ color: 'gray', marginTop: '1.5rem' }}>Stay Details</div>

                            <div className="display3Form">
                                <div className="DetailsBox stayDetails backgroundBlue"
                                    style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', }}>
                                    <div className="left">
                                        <div>CheckIn</div>
                                        <div>
                                            <strong
                                                style={{ fontWeight: '600' }}>{'-'}
                                            </strong>
                                        </div>
                                    </div>
                                    <div className="middle" >
                                        <div>{0}</div>
                                        <p style={{ marginBottom: '0.5rem' }}>Night</p>
                                    </div>
                                    <div className="right">
                                        <div>CheckOut</div>
                                        <div>
                                            <strong
                                                style={{ fontWeight: '600' }}>{'-'}
                                            </strong>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div style={{ color: 'gray' }}>Room Details</div>

                            {
                                [1, 2, 3].map((obj, ind) => {
                                    return (
                                        <>
                                            <div className="roomDetailsContainer"
                                                style={ind === [1, 2, 3].length - 1 ? { marginBottom: 0 } : {}}
                                            >
                                                <div className="roomImageSlider" >
                                                    <img id='img1' src={imagesArr[idRef.current]} alt="" ref={img1} />

                                                    <div className="leftSlide"><i className="bi bi-arrow-left-circle-fill" onClick={PrevImage}></i></div>
                                                    <div className="rightSlide"><i className="bi bi-arrow-right-circle-fill" onClick={NextImage}></i></div>
                                                </div>

                                                <div className="roomInfoBar">
                                                    <div className="roomTitle">
                                                        <div className="title">Double Delux Room</div>
                                                        {/* <div className="maxOccupancy">
                                        <div className='icon'>
                                            <i className="bi bi-person"></i>
                                        </div>
                                        <div className='num'>4</div>
                                    </div> */}
                                                    </div>
                                                    <div className="roomDetails">Lorem ipsum dolor, sit amet consectetur adipisicing elit. Perspiciatis nostrum ipsa quibusdam voluptas qui corrupti.</div>
                                                </div>

                                                <div className="roomFunctionalities">
                                                    <div className="functionalities">
                                                        <div className="lable">Select No. of Rooms</div>
                                                        <div className="calcs">
                                                            <div className="price">
                                                                <div className='amount'>Rs. {room * 9000}</div>
                                                                <div className="roomStatus">Room Only</div>
                                                            </div>
                                                            <div className="btnContainer">
                                                                <div className="minusBtn">
                                                                    <i className="bi bi-plus-lg" onClick={addRoom}></i>
                                                                </div>
                                                                <div className="value">{room}</div>
                                                                <div className="plusBtn">
                                                                    <i className="bi bi-dash-lg" onClick={subtractRoom}></i>
                                                                </div>
                                                            </div>
                                                        </div>

                                                    </div>
                                                </div>
                                            </div>
                                        </>
                                    )
                                })
                            }

                            <div className="gapBox" style={{ height: '120px' }}></div>
                        </>
                        :
                        <div className='noRoomAvailable'>
                            <img src="https://cdn.dribbble.com/users/2698098/screenshots/5957957/untitled-2-01_2x.jpg" alt="No Data Found" class="img-fluid"></img>
                            <div>Sorry no room available on this date</div>
                        </div>
                }
            </div>

            <Button btnClick={checkAndProceedToNextPage} btnText={'PROCEED'} iconClick={takeMeBack} iconTxt={`bi-arrow-left-short`} />
        </>
    )
}

export default Display2