import React, { useEffect } from "react";

// import bootstrap
import "bootstrap/dist/css/bootstrap.css";
import "bootstrap/dist/js/bootstrap.js";

// import bootstrap icon
import "bootstrap-icons/font/bootstrap-icons.css";

//react toastify styles
import "react-toastify/dist/ReactToastify.css";

// Custom Style Sheet
import "./assets/css/styles.min.css";
import { useState } from "react";

import MainDisplay from "./pages/MainDisplay";
import Display2 from "./pages/Display2";
import Display3 from "./pages/Display3";
import { useRef } from "react";
import { DARKMODE, LIGHTMODE } from "./Constant/ColorExport";
import { useSelector } from "react-redux"
import Loader from "./components/Loader/Loader";
import Header from "./components/Header/Header";
import bjLogo from "./assets/images/bj.png"


function App() {
  const { status } = useSelector((store) => store.status)

  const props = { position: 'bottom-left', logo: '', hotelId: 1953 }

  //STATE
  const [lightMode, setLightMode] = useState(true)
  const [loader, setLoader] = useState(true)
  const [STATUS, setSTATUS] = useState(1)
  const [topPos, setTopPos] = useState('bottom')
  ////////////////////////////////

  //REFS
  const widget_ref = useRef()
  const openCloseBtn_ref = useRef()
  ////////////////////////////////

  //SIDE-EFFECTS
  // Accessing scss variable using plain JavaScript and changing the same according to the state of "lightMode"
  useEffect(() => {
    const root = document.documentElement;
    root?.style.setProperty(
      "--MainColor",
      lightMode ? LIGHTMODE.MainColor : DARKMODE.MainColor
    );

    root?.style.setProperty(
      "--PrimaryBodyColor",
      lightMode ? LIGHTMODE.PrimaryBodyColor : DARKMODE.PrimaryBodyColor
    );
    root?.style.setProperty(
      "--PrimaryTextColor",
      lightMode ? LIGHTMODE.PrimaryTextColor : DARKMODE.PrimaryTextColor
    );
    root?.style.setProperty(
      "--BootstrapIconColor",
      lightMode ? LIGHTMODE.BootstrapIconColor : DARKMODE.BootstrapIconColor
    );
    root?.style.setProperty(
      "--BorderColor",
      lightMode ? LIGHTMODE.BorderColor : DARKMODE.BorderColor
    );
    // root?.style.setProperty(
    //   "--TextColorWhite",
    //   lightMode ? LIGHTMODE.TextColorWhite : DARKMODE.TextColorWhite
    // );
    // root?.style.setProperty(
    //   "--PrimaryCalenderText",
    //   lightMode ? LIGHTMODE.PrimaryCalenderText : DARKMODE.PrimaryCalenderText
    // );
    root?.style.setProperty(
      "--PrimaryCalenderBodyColor",
      lightMode ? LIGHTMODE.PrimaryCalenderBodyColor : DARKMODE.PrimaryCalenderBodyColor
    );
    // root?.style.setProperty(
    //   "--PrimaryCalenderDisabledBodyColor",
    //   lightMode ? LIGHTMODE.PrimaryCalenderDisabledBodyColor : DARKMODE.PrimaryCalenderDisabledBodyColor
    // );
    root?.style.setProperty(
      "--PrimaryCalenderDisabledText",
      lightMode ? LIGHTMODE.PrimaryCalenderDisabledText : DARKMODE.PrimaryCalenderDisabledText
    );
    root?.style.setProperty(
      "--PrimaryCalenderSelectedBodyColor",
      lightMode ? LIGHTMODE.PrimaryCalenderSelectedBodyColor : DARKMODE.PrimaryCalenderSelectedBodyColor
    );
    // root?.style.setProperty(
    //   "--PrimaryCalenderSelectedText",
    //   lightMode ? LIGHTMODE.PrimaryCalenderSelectedText : DARKMODE.PrimaryCalenderSelectedText
    // );
    root?.style.setProperty(
      "--DetailsBgColor",
      lightMode ? LIGHTMODE.DetailsBgColor : DARKMODE.DetailsBgColor
    );
    root?.style.setProperty(
      "--HeaderBorder",
      lightMode ? LIGHTMODE.HeaderBorder : DARKMODE.HeaderBorder
    );
    root?.style.setProperty(
      "--ChangeModeIconsColor",
      lightMode ? LIGHTMODE.ChangeModeIconsColor : DARKMODE.ChangeModeIconsColor
    );
  }, [lightMode]);

  useEffect(() => {
    setLoader(true)
    setTimeout(() => {
      setLoader(false)
      setSTATUS(status)
    }, 500);
  }, [status])
  ////////////////////////////////

  //EVENTS
  const handleVisibility = () => {
    widget_ref.current.style.top = '10%'
    widget_ref.current.style.opacity = 1
    widget_ref.current.style.transition = `
    top 0.3s ease-in,
    opacity 0.3s ease-in,
    `
    openCloseBtn_ref.current.className = "OPEN_CLOSE_BTN_NO_SEE"
  }

  ////////////Change position a/t props/////////////
  const getPosition = () => {
    const [vertical, horizontal] = props.position.split('-')
    const objV = new Object()
    const objH = new Object()
    objV.position = vertical
    objV.value = '25px'
    objH.position = horizontal
    objH.value = '25px'
    return [objV, objH]
  }

  useEffect(() => {
    if (!openCloseBtn_ref) return
    getPosition().map(obj => {
      const { position, value } = obj
      openCloseBtn_ref.current.style[position] = `${value}`
    })
  }, [props])

  useEffect(() => {
    if (!widget_ref) return
    getPosition().map(obj => {
      const { position, value } = obj
      if (position === 'left' || !position === 'right') {
        widget_ref.current.style[position] = `${value}`
      }
      else if (position === 'top' || !position === 'bottom') {
        widget_ref.current.style[position] = position === 'bottom' ? '125vh' : '-125vh'
        setTopPos(position)
      }
    })
  }, [props])

  useEffect(() => {
    if (!openCloseBtn_ref) return
    setTimeout(() => {
      openCloseBtn_ref.current.style.display = 'flex'
    }, 1000);
  }, [])


  return (
    <>
      <div className="OPEN_CLOSE_BTN" ref={openCloseBtn_ref} onClick={handleVisibility}>
        <div style={{ backgroundImage: props.logo ? `url(${props.logo})` : `url(${bjLogo})` }}></div>
      </div>


      <div className="widgetParentContainer" ref={widget_ref}>
        {
          loader ? <Loader /> :
            <>
              <Header openCloseBtn_ref={openCloseBtn_ref} widget_ref={widget_ref} status={STATUS} topPos={topPos} />
              {
                STATUS === 1 ? <MainDisplay hotelId={props.hotelId} /> :
                  STATUS === 2 ? <Display2 /> :
                    STATUS === 3 ? <Display3 /> : <></>
              }
            </>
        }
      </div>

      {/* <div className={lightMode ? "modeChangeBtn" : 'modeChangeBtnDark'} >
          <i className="bi bi-sun-fill"></i>
          <i className="bi bi-moon-stars-fill"></i>
          <div className={lightMode ? "modeBtn" : "modeBtnDark"} onClick={() => setLightMode(!lightMode)}></div>
        </div> */}
    </>
  );
}

export default App;
