import React, { useEffect, useRef, useState } from 'react'
import moment from "moment"
import { useDispatch } from "react-redux"
import { nextToPage2 } from '../Redux/Slices/StatusSlice';
import Button from '../components/Button/Button';
import { be_instance } from '../API/ApiExports';


function MainDisplay({ hotelId }) {
    const daysList = [
        'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'
    ]

    const dispatch = useDispatch()

    //STATES
    // ------------------------------api dependency
    const [dateValsDataArr, setDateValsDataArr] = useState([])
    // ------------------------------date selection
    const [selectedDatesArr, setSelectedDatesArr] = useState([])
    const [selectedFirstDateVal, setSelectedFirstDateVal] = useState('')
    const [selectedEndDateVal, setSelectedEndDateVal] = useState('')

    const [maxNumOfRoom, setMaxNumOfRoom] = useState(0)
    ////////////////////////////////

    //CALENDER-REFS
    const currentDate = useRef(new Date())
    const MonthName = useRef(moment(new Date()).format('MMMM'))
    const YearName = useRef(moment(new Date()).format('YYYY'))

    ////////////////////////////////

    //SIDE-EFFECTS
    useEffect(() => {
        if (!hotelId || !MonthName.current || !YearName.current) return
        getCalenderDataApi()
    }, [hotelId])

    const selectDatesInBetween = (startDate, endDate) => {
        var enumerateDaysBetweenDates = function (startDate, endDate) {
            var dates = [];
            startDate = startDate.add(1, 'days');
            while
                (startDate.format('M/D/YYYY') !== endDate.format('M/D/YYYY')) {
                dates.push(moment(startDate).format('D-MMMM-YYYY'));
                startDate = startDate.add(1, 'days');
            }
            dates.push(moment(endDate).format('D-MMMM-YYYY'))
            return dates;
        };
        return enumerateDaysBetweenDates(moment(startDate.split('-').reverse().join(' ')), moment(endDate.split('-').reverse().join(' ')))
    }

    const checkMinimumInventory = () => {
        const selectedDateInv = []
        dateValsDataArr.map(obj => {
            const { current_date, available_rooms } = obj
            if (available_rooms === undefined) return
            const date = moment(current_date).format('D-MMMM-YYYY')
            const includeVal = selectedDatesArr.includes(date)
            if (includeVal) {
                selectedDateInv.push(available_rooms)
            }
        })

        /////////////////////////
        const minRoomsArr = [...selectedDateInv]
        if (minRoomsArr.includes(0)) {
            minRoomsArr.pop()
            if (!minRoomsArr.includes(0)) {
                minRoomsArr.sort(function (a, b) { return a - b })
                setMaxNumOfRoom(minRoomsArr[0])
            }
        }
        else if (!minRoomsArr.includes(0)) {
            minRoomsArr.sort(function (a, b) { return a - b })
            setMaxNumOfRoom(minRoomsArr[0])
        }
        /////////////////////////

        selectedDateInv.pop()
        const isIncludeZero = selectedDateInv.includes(0)

        return isIncludeZero
    }

    ////////////////////////////////

    //EVENTS
    // const addAdult = () => { setAdults((prev) => prev + 1) }

    // const subtractAdult = () => {
    //     if (adults === 1) return
    //     setAdults((prev) => prev - 1)
    // }

    // const addChild = () => { setChildren((prev) => prev + 1) }

    // const subtractChild = () => {
    //     if (child === 0) return
    //     setChildren((prev) => prev - 1)
    // }

    //CALENDER EVENTS
    const moveToNextMonth = () => {
        setDateValsDataArr([])
        const currVal = moment(currentDate.current).format()
        currentDate.current = moment(currVal).add(1, 'months').format()

        MonthName.current = moment(currentDate.current).format('MMMM')
        YearName.current = moment(currentDate.current).format('YYYY')
        getCalenderDataApi()
    }

    const moveToPrevMonth = () => {
        setDateValsDataArr([])
        const currVal = moment(currentDate.current).format()
        currentDate.current = moment(currVal).subtract(1, 'months').format()

        MonthName.current = moment(currentDate.current).format('MMMM')
        YearName.current = moment(currentDate.current).format('YYYY')
        getCalenderDataApi()
    }

    const selectsDatesIntoListOnHover = (e, available_rooms) => {
        if (selectedFirstDateVal && selectedEndDateVal === '') {
            let arr = []

            if (selectedFirstDateVal.split('-')[1] === MonthName.current && selectedFirstDateVal.split('-')[2] === YearName.current) {
                for (let i = selectedFirstDateVal.split('-')[0]; i <= parseInt(e.target.id); i++) {
                    const hovered_date = [i, selectedFirstDateVal.split('-')[1], selectedFirstDateVal.split('-')[2]].join('-')
                    if (i > selectedFirstDateVal.split('-')[0]) {
                        arr.push(hovered_date)
                    }
                }
            }
            else if (selectedFirstDateVal.split('-')[1] !== MonthName.current || selectedFirstDateVal.split('-')[2] !== YearName.current) {
                if (moment(selectedFirstDateVal.split('-').reverse().join(' ')).isBefore([e.target.id, MonthName.current, YearName.current].join('-').split('-').reverse().join(' '))) {
                    arr = selectDatesInBetween(selectedFirstDateVal, [e.target.id, MonthName.current, YearName.current].join('-'))
                }
            }
            setSelectedDatesArr([selectedFirstDateVal, ...arr])
        }
    }

    const checkSelectedDatesStatus = () => {
        if (selectedFirstDateVal && !selectedEndDateVal) {
            const selected_date = selectedFirstDateVal
            setSelectedDatesArr([selected_date])
        }
    }

    //ROUTE EVENT
    const checkAndProceedToNextPage = () => {
        dispatch(nextToPage2())
    }

    ////////////////////////////////

    //API_INTEGRATION

    async function getCalenderDataApi() {
        try {
            const { data } = await be_instance.get(`get-be-ari/${hotelId}/${MonthName.current}/${YearName.current}`)
            if (data.status === 1) {
                const apiData = data.data

                const startOfMonth_DayName = moment(currentDate.current).startOf('month').format('ddd')

                const null_days = daysList.indexOf(startOfMonth_DayName);

                const datesArr = []

                for (let i = 1; i <= null_days; i++) {
                    datesArr.push(0)
                }
                const dates = moment(currentDate.current).daysInMonth()

                for (let i = 1; i <= dates; i++) {
                    datesArr.push(i)
                }

                // datesArr || apiData

                const arr = [...datesArr]

                const vals = arr.map(val => {
                    if (val)
                        return { dateVal: val, ...apiData[val - 1] }
                    else return { dateVal: val }
                })
                setDateValsDataArr(vals)
            }

            else setDateValsDataArr([])
        }
        catch (error) {
            console.log(error)
            setDateValsDataArr([])
        }
    }
    ////////////////////////////////


    return (
        <>
            <div className="body">
                <div className="calender">
                    <div className="monthDisplaySect">
                        <div className="left">
                            <div className="monthName">{MonthName.current}</div>
                            <div className="year">{YearName.current}</div>
                        </div>
                        <div className="right">
                            <i className="bi bi-chevron-left"
                                onClick={moment().format('MMMM').toLowerCase() === MonthName.current.toLowerCase() && moment().format('YYYY') === YearName.current
                                    ? () => { } : () => moveToPrevMonth()}  >
                            </i>
                            <i className="bi bi-chevron-right" onClick={moveToNextMonth}></i>
                        </div>
                    </div>

                    <div className="daysDisplaySect">
                        {
                            daysList.map((day, id) => {
                                return (
                                    <div key={id} className="day">{day}</div>
                                )
                            })
                        }
                    </div>
                    <div className="datesDisplaySect" onMouseLeave={checkSelectedDatesStatus}>
                        {
                            dateValsDataArr.length ?
                                dateValsDataArr.map((dateObj, id) => {
                                    const { available_rooms, dateVal } = dateObj
                                    return (
                                        !dateVal ?
                                            <div key={id}></div>
                                            :
                                            <React.Fragment key={id} >
                                                {
                                                    moment(`${YearName.current}-${moment().month(MonthName.current).format("M")}-${dateVal}`) < moment()
                                                        ?
                                                        <div
                                                            className={'dateHolderDisabled'}>
                                                            <div className="date">{dateVal}</div>
                                                        </div>
                                                        :
                                                        <div
                                                            className={
                                                                `
                                                                ${available_rooms ?
                                                                    selectedDatesArr.includes(`${dateVal}-${MonthName.current}-${YearName.current}`) ?
                                                                        `dateHolderSelected` :
                                                                        'dateHolder'
                                                                    : 'dateHolderDisabled blockDate'
                                                                }
                                                                `
                                                            }
                                                            style={{ position: 'relative' }}
                                                        >
                                                            <>
                                                                <div className="date" style={available_rooms ? {} : { color: 'white' }}>{dateVal}</div>

                                                                {
                                                                    available_rooms ?
                                                                        <div className="amount">
                                                                            <div className='keyRotate'>
                                                                                <i className="bi bi-key" style={{ fontSize: '17px' }}></i>
                                                                            </div>
                                                                            <div style={{ marginLeft: '0.25rem' }}>{available_rooms}</div>
                                                                        </div>
                                                                        :
                                                                        <div className="amount" style={{ color: 'white' }}>-</div>
                                                                }

                                                                <div id={dateVal}
                                                                    onClick={() => {
                                                                        if (!selectedFirstDateVal && !selectedEndDateVal) {
                                                                            setSelectedFirstDateVal([dateVal, MonthName.current, YearName.current].join('-'));
                                                                            setSelectedDatesArr([[dateVal, MonthName.current, YearName.current].join('-')])
                                                                        }

                                                                        else if (selectedFirstDateVal && !selectedEndDateVal) {
                                                                            if (!checkMinimumInventory()) {
                                                                                if (moment(selectedFirstDateVal.split('-').reverse().join(' ')).isBefore([dateVal, MonthName.current, YearName.current].join('-').split('-').reverse().join(' ')) === true) {
                                                                                    setSelectedEndDateVal([dateVal, MonthName.current, YearName.current].join('-'));
                                                                                }

                                                                                else if (moment(selectedFirstDateVal.split('-').reverse().join(' ')).isBefore([dateVal, MonthName.current, YearName.current].join('-').split('-').reverse().join(' ')) === false) {
                                                                                    setSelectedFirstDateVal([dateVal, MonthName.current, YearName.current].join('-'));
                                                                                    // setSelectedDatesArr([])
                                                                                    setSelectedDatesArr([[dateVal, MonthName.current, YearName.current].join('-')])
                                                                                }
                                                                            }
                                                                        }

                                                                        else if (selectedFirstDateVal && selectedEndDateVal) {
                                                                            setSelectedFirstDateVal([dateVal, MonthName.current, YearName.current].join('-'));
                                                                            setSelectedEndDateVal('');
                                                                            // setSelectedDatesArr([])
                                                                            setSelectedDatesArr([[dateVal, MonthName.current, YearName.current].join('-')])
                                                                        }
                                                                    }}
                                                                    onMouseEnter={(e) => { selectsDatesIntoListOnHover(e, available_rooms) }}
                                                                    style={{
                                                                        width: '100%',
                                                                        height: '100%',
                                                                        position: 'absolute',
                                                                        left: '0',
                                                                        top: '0',
                                                                        cursor: 'pointer'
                                                                    }}>
                                                                </div>
                                                            </>
                                                        </div>
                                                }
                                            </React.Fragment>
                                    )
                                })
                                :
                                [...Array(42).keys()].map((e) => {
                                    return (
                                        <div
                                            className='dateHolder skeleton' key={e}
                                        >
                                        </div>
                                    )
                                })
                        }
                    </div>
                </div>


                <div style={{
                    height: '1px',
                    border: '0',
                    borderTop: '1px solid #CED4DA',
                    margin: '2rem 0',
                    padding: '0',
                }}></div>


                <div className="checkAvailabilityPayloadContainer">
                    {/* <div className="adult payload">
                        <div className='lable'>Adult</div>
                        <div className="btnContainer">
                            <div className="minusBtn">
                                <i className="bi bi-plus-lg" onClick={addAdult}></i>
                            </div>
                            <div className="value">{adults}</div>
                            <div className="plusBtn">
                                <i className="bi bi-dash-lg" onClick={subtractAdult}></i>
                            </div>
                        </div>
                    </div>

                    <div className="child payload">
                        <div className='lable'>Child</div>
                        <div className="btnContainer">
                            <div className="minusBtn">
                                <i className="bi bi-plus-lg" onClick={addChild}></i>
                            </div>
                            <div className="value">{child}</div>
                            <div className="plusBtn">
                                <i className="bi bi-dash-lg" onClick={subtractChild}></i>
                            </div>
                        </div>
                    </div>

                    <div className="mealPlan payload">
                        <div className='lable'>Meal Plan</div>
                        <div className="selectContainer">
                            <div>select box</div>
                        </div>
                    </div> */}

                    <div style={{ color: 'gray' }}>Stay Details</div>

                    <div className="display3Form">
                        <div className="DetailsBox stayDetails backgroundBlue"
                            style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', }}>
                            <div className="left">
                                <div>CheckIn</div>
                                <div>
                                    <strong
                                        style={{ fontWeight: '600' }}>{selectedFirstDateVal && selectedEndDateVal ? selectedFirstDateVal.replaceAll('-', ' ') : '-'}
                                    </strong>
                                </div>
                            </div>
                            <div className="middle" >
                                <div>{selectedFirstDateVal && selectedEndDateVal ? selectedDatesArr.length - 1 : 0}</div>
                                <p style={{ marginBottom: '0.5rem' }}>Night</p>
                            </div>
                            <div className="right">
                                <div>CheckOut</div>
                                <div>
                                    <strong
                                        style={{ fontWeight: '600' }}>{selectedFirstDateVal && selectedEndDateVal ? selectedEndDateVal.replaceAll('-', ' ') : '-'}
                                    </strong>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div
                        className="
                        checkInCheckOutdateContainer payload "
                        style={{
                            display: 'flex', justifyContent: 'space-between'
                        }}>
                        <div>Number Of Rooms</div>

                        <select id="roomNum" name="roomNum" onChange={(e) => {
                        }} style={{
                            background: "#F2F2F2", width: '120px', height: '35px',
                            borderRadius: '0.5rem',
                            border: '1px solid lightgray',
                            textAlign: "center",
                        }}>
                            <option value="">Select Rooms</option>
                            {
                                [...Array(maxNumOfRoom).keys()].map(id => {
                                    return (
                                        <option value={id + 1} className='option' key={id + 1} style={{
                                            height: '25px', borderBottom: '1px solid lightgray', textAlign: 'center'
                                        }}>
                                            {id + 1}
                                        </option>
                                    )
                                })
                            }
                        </select>
                    </div>
                </div>

                <div style={{ width: '100%', height: '100px' }}></div>
            </div>

            <Button btnClick={checkAndProceedToNextPage} btnText={'PROCEED'} />
        </>
    )
}

export default MainDisplay