import React from "react";
import ReactDOM from "react-dom/client";
import App from "./App";
import Store from "./Redux/Store";
import { Provider } from "react-redux";

const allWidgets = [...document.querySelectorAll(".react-widget")];
const allVals = allWidgets.map((node) => {
  return ReactDOM.createRoot(node);
});

allVals.map((val) => {
  val.render(
    <React.StrictMode>
      <Provider store={Store}>
        <App symbol={val._internalRoot.containerInfo.dataset.symbol} />
      </Provider>
    </React.StrictMode>
  );
});
