import React, { useRef, useState } from 'react'

function Header({ openCloseBtn_ref, widget_ref, status, topPos }) {
    const currencyTypes = ['INR', 'CHN', 'KOR', 'JAP', 'INDO', 'EUR', 'USD']

    //STATES
    const [currencyType, setSelectedCurrncyType] = useState('INR')

    //REFS
    const currencyTypesList_ref = useRef()
    const chevronIconRef = useRef()

    //EVENTS
    const handleClick = () => {
        const classList = [...currencyTypesList_ref.current.classList]
        if (classList.includes('currencyTypesList')) {
            chevronIconRef.current.className = 'bi bi-chevron-up'
            currencyTypesList_ref.current.className = 'currencyTypesListHeight'
        }
        else if (classList.includes('currencyTypesListHeight')) {
            chevronIconRef.current.className = 'bi bi-chevron-down'
            currencyTypesList_ref.current.className = 'currencyTypesList'
        }
    }

    const handleVisibility = () => {
        widget_ref.current.style.top = topPos === 'bottom' ? '125vh' : '-125vh'
        widget_ref.current.style.opacity = 0
        widget_ref.current.style.transition = `
        top 0.3s ease-in, 
        opacity 0.3s ease-in, 
        `
        openCloseBtn_ref.current.className = "OPEN_CLOSE_BTN"
    }


    return (
        <div className="header">
            <div className="left">
                <div>Instant Booking</div>
            </div>
            {/* <div className="middle">
                <div>Step {status} of 2</div>
            </div> */}
            <div className="right">
                {/* <div className='accordion'>
                    <div id='currencyType'>{currencyType}</div>
                    <div ref={currencyTypesList_ref} className="currencyTypesList"
                    >
                        {
                            currencyTypes.map(cur => {
                                return (
                                    <div
                                        style={{
                                            display: "flex",
                                            justifyContent: "center",
                                            alignItems: "center",
                                            padding: "0.5rem 0",
                                            cursor: 'pointer',
                                        }} key={cur}
                                        onClick={() => setSelectedCurrncyType(cur)}
                                    >
                                        {cur}
                                    </div>
                                )
                            })
                        }
                    </div>
                    <i ref={chevronIconRef} className="bi bi-chevron-down" onClick={handleClick}></i>
                </div> */}
                <i className="bi bi-x-lg" style={{ cursor: 'pointer' }} onClick={handleVisibility}></i>
            </div>

        </div>
    )
}

export default Header