import React from 'react'
import Button from '../components/Button/Button'
import { useDispatch } from "react-redux"
import { prevToPage2 } from '../Redux/Slices/StatusSlice'
import { useState } from 'react'
import { useRef } from 'react'


function Display3() {
    const dispatch = useDispatch()

    const mailformat = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/

    const capitalize = (string) => {
        if (!string) return ''
        return string[0].toUpperCase() + string.substring(1)
    }
    /////////////////////////////////////////////

    //STATES
    const [name, setName] = useState('')
    const [mail, setMail] = useState('')
    const [phone, setPhone] = useState('')
    const [checkMyself, setCheckMyself] = useState(false)
    const [checkBusiness, setCheckBusiness] = useState(false)
    /////////////////////////////////////////////

    //REFS
    const nameRef = useRef()
    const mailRef = useRef()
    const phoneRef = useRef()
    /////////////////////////////////////////////

    //EVENTS
    const payNowOption = () => {
        if (!name) nameRef.current.style.visibility = 'visible';
        else if (!mail) mailRef.current.style.visibility = 'visible';
        else if (mail && !mail.match(mailformat)) mailRef.current.style.visibility = 'visible';
        else if (!phone) phoneRef.current.style.visibility = 'visible';
        else {
            console.log('func called')
        }
    }

    const takeMeBack = () => {
        dispatch(prevToPage2())
    }

    const setInputType = (e) => {
        if (e.target.value === 'myself') {
            setCheckMyself(true)
            setCheckBusiness(false)
        }
        else if (e.target.value === 'business') {
            setCheckMyself(false)
            setCheckBusiness(true)
        }
    }


    return (
        <>
            <div className="body">
                <div className="display3Form">
                    <div style={{ color: 'gray', marginBottom: '1.5rem' }}>Stay Details</div>
                    <div className="DetailsBox stayDetails" style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', }}>
                        <div className="left">
                            <div>CheckIn</div>
                            <div>
                                <strong>Mon, </strong>
                                <strong>12 Aug 2022</strong>
                            </div>
                        </div>
                        <div className="middle" >
                            <div>1</div>
                            <p style={{ marginBottom: '0.5rem' }}>Night</p>
                        </div>
                        <div className="right">
                            <div>CheckOut</div>
                            <div>
                                <strong>Tue, </strong>
                                <strong>13 Aug 2022</strong>
                            </div>
                        </div>
                    </div>

                    <div style={{ color: 'gray', marginBottom: '1.5rem' }}>Room Details</div>
                    <div className="DetailsBox" style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                        <div className="left">
                            <strong style={{ display: 'inline-block', }}>Double Delux Room</strong>

                            <div style={{ fontSize: '14px', }}>Room Only</div>

                            <div >
                                <strong>2</strong>
                                <div style={{ fontSize: '14px', display: 'inline' }}> Rooms</div>
                            </div>

                            <div>
                                <strong>2</strong>
                                <div style={{ fontSize: '14px', display: 'inline' }}> Adults, </div>
                                <strong>0</strong>
                                <div style={{ fontSize: '14px', display: 'inline' }}> Child</div>
                            </div>
                        </div>
                        <div className="right">
                            <strong>Rs. 9000</strong>
                            <div style={{ fontSize: '14px' }}>Per Night</div>
                        </div>
                    </div>

                    <div style={{ color: 'gray', marginBottom: '1.5rem' }}>Total amount to be paid:</div>
                    <div className="DetailsBox" style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                        <div>Total</div>
                        <strong>Rs. 18000</strong>
                    </div>

                    <div className='radioBtnContainer'>
                        <div style={{ color: 'gray', }}>Guest Details</div>
                        <fieldset className="radioBtns">
                            <label htmlFor="myself">Myself</label>
                            <input type="radio" name="myself" id="myself" value="myself" checked={checkMyself} onChange={(e) => {
                                setInputType(e)
                            }} />

                            <label htmlFor="business">Business</label>
                            <input type="radio" name="business" id="business" value="business" checked={checkBusiness} onChange={(e) => {
                                setInputType(e)
                            }} />
                        </fieldset>
                    </div>

                    {/* ////////////////////////////////////////////////////// */}
                    <div style={{ color: 'gray', marginBottom: '1.5rem' }}>Guest Name</div>
                    <div className="DetailsBox InputBox" >
                        <input type="text" placeholder='exp: Rohan Arora' value={name}
                            onChange={(e) => {
                                nameRef.current.style.visibility = 'hidden';
                                setName(capitalize(e.target.value.replace(/[^A-Za-z ]/g, '')))
                            }}
                        />
                    </div>
                    <small ref={nameRef}>Please provide a name</small>

                    <div style={{ color: 'gray', marginBottom: '1.5rem' }}>Email Address</div>
                    <div className="DetailsBox InputBox" >
                        <input type="text" placeholder='exp: email@gmail.com' value={mail}
                            onChange={(e) => {
                                mailRef.current.style.visibility = 'hidden';
                                setMail(e.target.value)
                            }}
                        />
                    </div>
                    <small ref={mailRef}>Please provide a valid mail</small>

                    <div style={{ color: 'gray', marginBottom: '1.5rem' }}>Phone Number</div>
                    <div className="DetailsBox InputBox" >
                        <input type="text" placeholder='exp: 9337615147' value={phone}
                            onChange={(e) => {
                                phoneRef.current.style.visibility = 'hidden';
                                const value = parseInt(e.target.value)
                                setPhone(value ? value : '')
                            }}
                        />
                    </div>
                    <small ref={phoneRef}>Please provide a valid number</small>

                    <div style={{ width: '100%', height: '100px' }}></div>
                </div>
            </div>
            <Button btnClick={payNowOption} btnText={'PAY NOW'} iconClick={takeMeBack} iconTxt={`bi-arrow-left-short`} />
        </>
    )
}

export default Display3